input[type="range"] {
  width: 100%;
  height: 20px;
  margin: 10px 0;
  -webkit-appearance: none;
}
input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 0.7rem;
  border-radius: 1rem;
  cursor: pointer;
  box-shadow: var(--shadow-active);
}
input[type="range"]::-moz-range-thumb {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  background: var(--primary);
  box-shadow: 0px 0.1rem 0.3rem 0px var(--greyLight-3);
}

/* Webkit */

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 0.7rem;
  border-radius: 1rem;
  cursor: pointer;
  box-shadow: var(--shadow-active);
}

input[type="range"]::-webkit-slider-thumb {
  width: 1.5rem;
  height: 1.5rem;
  margin-top: -7px;
  border-radius: 50%;
  cursor: pointer;
  background: var(--primary);
  box-shadow: 0px 0.1rem 0.3rem 0px var(--greyLight-3);
  -webkit-appearance: none;
}

