.App_input_unique_label__2VV4r {
  display: flex;
  align-items: center;
  width: 70px;
  height: 30px;
  box-shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2),
    -0.2rem -0.2rem 0.5rem var(--white);
  background: rgba(255, 255, 255, 0);
  position: relative;
  cursor: pointer;
  border-radius: 1.6rem;
}
.App_input_unique_label__2VV4r::after {
  content: "";
  position: absolute;
  left: 0.4rem;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: var(--greyDark);
  transition: all 0.4s ease;
}
.App_input_unique_label__2VV4r::before {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background: linear-gradient(
    330deg,
    var(--primary-dark) 0%,
    var(--primary) 50%,
    var(--primary-light) 100%
  );
  opacity: 0;
  transition: all 0.4s ease;
}
.App_input_unique__3WEqR input:checked ~ .App_input_unique_label__2VV4r::before {
  opacity: 1;
}
.App_input_unique__3WEqR input:checked ~ .App_input_unique_label__2VV4r::after {
  left: 61%;
  background: var(--greyLight-1);
}

.App_input_unique_span__3gSQM {
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: var(--transition);
}

